const canvas = document.getElementsByTagName('canvas')[0];

window.addEventListener('resize', resizeCanvas, false);
console.log(window.devicePixelRatio);

let canvasW: number, canvasH: number, pixe: number;
function resizeCanvas() {
    pixe = window.devicePixelRatio;
    canvasW = canvas.width = window.innerWidth * pixe;
    canvasH = canvas.height = window.innerHeight * pixe;
}

resizeCanvas();
const ctx = canvas.getContext('2d');

export { canvas, canvasW, canvasH, pixe, ctx };
