import Sprite from './sprite';
import { Zd } from './zd';
import { canvas, pixe, ctx, canvasW, canvasH } from './canvas';

export class Player extends Sprite {
    istouch: boolean = false;
    zd: Zd[] = [];
    time: number = 0;
    img: HTMLImageElement;
    constructor(ctx: CanvasRenderingContext2D, x: number, y: number, w: number = 186, h: number = 130) {
        super(ctx, x, y, w, h);
        this.img = new Image();

        // img.onload = () => {
        //     this.img = img;
        // };
        this.img.src = './img/hero.png';
        this.init();
    }

    init() {
        canvas.addEventListener('touchstart', e => {
            const box = canvas.getBoundingClientRect();
            const x = ((e.touches[0].clientX - box.left) * canvas.width) / box.width;
            const y = ((e.touches[0].clientY - box.top) * canvas.height) / box.height;
            console.log(x, (e.touches[0].clientX - box.left) * pixe);

            if (x >= this.x && x <= this.x + this.w && y >= this.y && y <= this.y + this.h) {
                this.istouch = true;
            }
        });

        canvas.addEventListener('touchmove', e => {
            console.log(e.touches[0]);
            if (this.istouch) {
                // const x = e.touches[0].clientX * pixe;
                // const y = e.touches[0].clientY * pixe;
                const box = canvas.getBoundingClientRect();
                const x = ((e.touches[0].clientX - box.left) * canvas.width) / box.width;
                const y = ((e.touches[0].clientY - box.top) * canvas.height) / box.height;
                this.x = x - this.w / 2;
                this.y = y - this.h / 2;
            }
        });

        canvas.addEventListener('touchend', e => {
            console.log('touchend');

            this.istouch = false;
        });
    }
    reset() {
        this.zd = [];
        this.x = canvasW / 2;
        this.y = canvasH - 400;
    }
    drawToCanvas() {
        if (this.img.complete) {
            // ctx.drawImage(this.img, this.x, this.y, this.w, this.h);
            ctx.fillRect(this.x, this.y, this.w, this.h);
        }
    }
    animation() {
        this.drawToCanvas();

        // if (this.zd.length < 5) {
        const t = new Date().getTime();
        if (t - this.time > 100) {
            this.zd.push(new Zd(ctx, this.x + this.w / 2, this.y));
            this.time = t;
        }

        //  }

        this.zd.forEach((item, index) => {
            if (item.visible) {
                item.animation(15);
            } else {
                this.zd.splice(index, 1);
            }
        });
    }
}
