import Sprite from './sprite';

export class Zd extends Sprite {
    visible: boolean = true;
    constructor(ctx: CanvasRenderingContext2D, x: number, y: number, w: number = 10, h: number = 15) {
        super(ctx, x, y, w, h);
    }
    drawToCanvas() {
        this.ctx.fillStyle = '#000';
        this.ctx.fillRect(this.x, this.y, this.w, this.h);
    }
    animation(speed: number) {
        this.y -= speed;
        this.drawToCanvas();
        if (this.y < 0) {
            this.visible = false;
        }
    }
}
