import Sprite from './sprite';
import { Player } from './player';
import { canvasH, ctx, pixe, canvasW, canvas } from './canvas';
import axios from 'axios';
import Vue from './vue';

let Api = 'http://api.fanep.cn';

if (process.env.NODE_ENV === 'development') {
    Api = 'http://192.168.2.88:3001';
}

//window.requestAnimationFrame(run);
const formatDate = function(date, fmt = 'YYYY-MM-DD HH:mm:ss') {
    if (typeof date === 'string') {
        date = new Date(date.replace(/-/g, '/'));
    }
    if (typeof date === 'number') {
        date = new Date(date);
    }

    var o = {
        'M+': date.getMonth() + 1,
        'D+': date.getDate(),
        'h+': date.getHours() % 12 === 0 ? 12 : date.getHours() % 12,
        'H+': date.getHours(),
        'm+': date.getMinutes(),
        's+': date.getSeconds(),
        'q+': Math.floor((date.getMonth() + 3) / 3),
        S: date.getMilliseconds()
    };

    var week = {
        '0': '\u65e5',
        '1': '\u4e00',
        '2': '\u4e8c',
        '3': '\u4e09',
        '4': '\u56db',
        '5': '\u4e94',
        '6': '\u516d'
    };
    if (/(Y+)/.test(fmt)) {
        fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length));
    }
    if (/(E+)/.test(fmt)) {
        fmt = fmt.replace(
            RegExp.$1,
            (RegExp.$1.length > 1 ? (RegExp.$1.length > 2 ? '\u661f\u671f' : '\u5468') : '') + week[date.getDay() + '']
        );
    }
    for (var k in o) {
        if (new RegExp('(' + k + ')').test(fmt)) {
            fmt = fmt.replace(
                RegExp.$1,
                RegExp.$1.length === 1 ? String(o[k]) : ('00' + o[k]).substr(('' + o[k]).length)
            );
        }
    }
    return fmt;
};

class Enemy extends Sprite {
    id: symbol;
    visible: boolean = true;
    constructor(ctx: CanvasRenderingContext2D, x: number, y: number) {
        super(ctx, x, y, 20, 30);
        this.id = Symbol();
    }
    animation(speed: number) {
        this.y += speed;
        if (this.y > canvasH) {
            this.visible = false;
        }
        this.drawToCanvas();
    }
}

//
class Databus {
    enemys: Enemy[];
    runId: number;
    enemyNum: number = 10; //
    speed: number = 1; //
    constructor() {
        this.enemys = [];
    }
    createBullet() {
        const enemy = new Enemy(ctx, Math.random() * canvasW, 0);
        this.enemys.push(enemy);
    }
    removeBullet(index: number) {
        this.enemys.splice(index, 1);
    }

    animation() {
        this.enemys.map((item, index) => {
            if (item.y > canvasH || item.y < 0 || item.x < 0 || item.x > canvasW) {
                this.removeBullet(index);
            }
            item.animation(this.speed);
        });
        if (this.enemys.length <= this.enemyNum) {
            this.createBullet();
        }
    }
}

const dataBus = new Databus();
const player = new Player(ctx, canvasW / 2, canvasH - 400);
class Main {
    overBtn = [(canvasW - 400) / 2, 300, 400, 200];
    rankBtn = [(canvasW - 400) / 2, 600, 400, 200];
    isOver = false;
    callbackId: number;
    score: number = 0;
    maxScore: number = 0;
    run: () => void;
    animationList = [];

    constructor() {
        ///
        this.animationList.push(player.animation.bind(player));
        this.animationList.push(dataBus.animation.bind(dataBus));

        this.run = () => {
            this.callbackId = window.requestAnimationFrame(this.run);
            ctx.clearRect(0, 0, canvasW, canvasH);
            this.animationList.forEach(item => {
                item();
            });
            this.renderScore();

            dataBus.enemys.forEach((item, index) => {
                player.zd.forEach((bullet, i) => {
                    if (bullet.isCoincide(item)) {
                        this.score += 1;
                        player.zd.splice(i, 1);
                        dataBus.enemys.splice(index, 1);
                    }
                });
                if (item.isCoincide(player)) {
                    window.cancelAnimationFrame(this.callbackId);
                    console.log('');
                    this.isOver = true;
                    this.renderGameOver();
                    this.renderRankBtn();
                }
            });
        };
        this.run();
        this.touchBind();
    }
    touchBind() {
        canvas.addEventListener('touchstart', e => {});

        canvas.addEventListener('touchmove', e => {
            e.preventDefault();
        });

        canvas.addEventListener('touchend', e => {
            //  console.log(e);
        });
        canvas.addEventListener('click', e => {
            if (this.isOver) {
                let x = e.clientX * pixe;
                let y = e.clientY * pixe;
                const [x1, y1, w, h] = this.overBtn;
                const [x2, y2, w1, h1] = this.rankBtn;
                //
                if (x > x1 && x < x1 + w && y > y1 && y < y1 + h) {
                    this.restart();
                }

                // 
                if (x > x2 && x < x2 + w1 && y > y2 && y < y2 + h1) {
                    //this.restart();
                    v.show = true;
                }
            }
        });
    }
    restart() {
        //
        dataBus.enemys = [];
        this.score = 0;
        player.reset();
        this.callbackId = window.requestAnimationFrame(this.run);
    }
    renderGameOver() {
        ctx.fillStyle = 'green';
        const [a, b, c, d] = this.overBtn;
        ctx.fillRect(a, b, c, d);
        ctx.fillStyle = '#fff';
        ctx.font = '50px serif';

        ctx.fillText('', a + 50, b + 50);
        ctx.fillText('' + this.score, a + 50, b + 100);
        this.postScore();
    }
    renderRankBtn() {
        ctx.fillStyle = 'green';
        const [a, b, c, d] = this.rankBtn;
        ctx.fillRect(a, b, c, d);
        ctx.fillStyle = '#fff';
        ctx.font = '50px serif';

        ctx.fillText('', a + 50, b + 50);
    }
    renderScore() {
        ctx.fillStyle = '#000';
        ctx.font = '40px serif';
        ctx.fillText(': ' + this.score, 20, 30);
        ctx.fillText(': ' + this.maxScore, 20, 70);
    }
    postScore() {
        axios.post(Api + '/api/postgamescore', {
            name: v.name || '',
            score: this.score
        });
    }
}

const game = new Main();
const v = new Vue({
    el: '#mine',
    data: {
        show: false,
        rank: [],
        name: '',
        postnamevue: false
    },
    filters: {
        formatDate(date: string) {
            return formatDate(Number(date));
        }
    },
    methods: {
        postname() {
            if (this.name) {
                this.postnamevue = false;
                this.show = false;
                localStorage.setItem('username', this.name);
            }
        },
        getRankList() {
            axios
                .get(Api + '/api/getgamemax')
                .then(Response => Response.data)
                .then(data => {
                    console.log(data.data.list);
                    game.maxScore = data.data.list[0].score;
                    this.rank = data.data.list;
                });
        }
    },
    watch: {
        show(val) {
            if (val) {
                this.getRankList();
            }
        }
    },
    mounted() {
        this.name = localStorage.getItem('username');
        //this.show = true;
        // this.getRankList();
        if (!this.name) {
            this.show = true;
            this.postnamevue = true;
        }
    }
});
