export default class Sprite {
    x: number;
    y: number;
    w: number;
    h: number;
    ctx: CanvasRenderingContext2D;
    constructor(ctx: CanvasRenderingContext2D, x: number, y: number, w: number, h: number) {
        this.ctx = ctx;
        this.x = x;
        this.y = y;
        this.w = w;
        this.h = h;
    }
    drawToCanvas() {
        this.ctx.fillStyle = 'red';
        this.ctx.fillRect(this.x, this.y, this.w, this.h);
    }

    isCoincide(sprite: Sprite) {
        const lt =
            this.x > sprite.x && this.x < sprite.x + sprite.w && this.y > sprite.y && this.y < sprite.y + sprite.h;
        const rt =
            this.x + this.w > sprite.x &&
            this.x + this.w < sprite.x + sprite.w &&
            this.y > sprite.y &&
            this.y < sprite.y + sprite.h;
        const lb =
            this.x > sprite.x &&
            this.x < sprite.x + sprite.w &&
            this.y + this.h > sprite.y &&
            this.y + this.h < sprite.y + sprite.h;
        const rb =
            this.x + this.w > sprite.x &&
            this.x + this.w < sprite.x + sprite.w &&
            this.y + this.h > sprite.y &&
            this.y + this.h < sprite.y + sprite.h;

        if (lt || rt || lb || rb) {
            return true;
        } else {
            return false;
        }
    }
}
